/* main2/SecondSection.module.css */

@keyframes netflixIntro {
    0% {
        opacity: 0;
        transform: translateX(100%);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

.secondSection {
    width: 100%;
    height: 100vh;
    background-color: black; /* 검은색 배경 */
    display: flex;
    justify-content: center;
    align-items: center;
}

.secondSectionText {
    opacity: 0;
    font-size: 5vw;
    color: white;
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7);
    animation: netflixIntro 3s ease-in-out forwards; /* 넷플릭스 인트로 애니메이션 */
}